import "./styles.css";
import _ from "./lib";

const setImageData = (obj) => {
  let myImg = _.$("#app img");
  myImg.setAttribute("title", "Foto: " + obj.author);
  myImg.src = obj.download_url;
};
const setStatus = (status) => {
  let el = _.$("#ladeStatus").classList;
  if (status === "ready") {
    el.remove("loading");
  } else {
    el.remove("ready");
  }
  el.add(status);
};

const setLoadError = (type, text) => {
  _.$("#s1 figcaption").innerText = text;
  _.$("#s1 figcaption").classList.toggle(type);
};

const url = "https://picsum.photos/id/232/info";
const myApp = (_.$("#app").innerHTML = `
<section id="s1">
<h1>Hallo Leute</h1>
<h2>Hier steht die Unter√ºberschrift</h2>
<p>Zeit: <span id="time"></span></p>
<p id="ladeStatus" class="ready"></p>
<figure>
  <img alt="Hier sollte ein Bild zu sehen sein" width="300" title="Bild" src=""/>
  <figcaption></figcaption>
</figure>
<p><button id="load">Bild Laden</button></p>
</section>
`);
setInterval(() => {
  _.$("#s1 #time").innerText = new Date().toLocaleTimeString();
}, 1000);
_.on("load", _.$("img"), () => setStatus("ready"));
_.on("click", _.$("#load"), (ev) => {
  setStatus("loading");
  setLoadError("ok", "");
  fetch(url)
    .then((res) => res.json())
    .then((obj) => setImageData(obj))
    .catch(() => setLoadError("error", "Fehler beim Download!"));
  //    .finally(setStatus("ready"));
});
